.menu {
  position: fixed;
  background: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  list-style: none;
  margin: 0;
  padding: 0.5rem 0;
  z-index: 1000;
  min-width: 180px;
}

.menu li {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  cursor: pointer;
  gap: 0.5rem;
  font-size: 14px;
  transition: background 0.2s;
}

.menu li:hover {
  background: #f1f1f1;
}

.menu li.disabled {
  color: #aaa;
  cursor: not-allowed;
}

.icon {
  width: 1rem;
  text-align: center;
}