import { Component, h, Prop, State, Listen, Element } from '@stencil/core';

export interface MenuItem {
  name: string;
  icon: string; // Icon class or emoji
  enabled: boolean;
}

@Component({
  tag: 'right-click-menu',
  styleUrl: 'right-click-menu.css',
  shadow: true,
})
export class RightClickMenu {
  @Element() el!: HTMLElement;

  @Prop() items: MenuItem[] = [];

  @State() visible = false;
  @State() pos = { x: 0, y: 0 };

  @Listen('contextmenu', { target: 'window' })
  onContextMenu(event: MouseEvent) {
    event.preventDefault();
    this.visible = true;
    this.pos = { x: event.clientX, y: event.clientY };
  }

  @Listen('click', { target: 'window' })
  onClick() {
    this.visible = false;
  }

  private onItemClick(item: MenuItem) {
    if (item.enabled) {
      const customEvent = new CustomEvent('menuSelect', {
        detail: item,
        bubbles: true,
        composed: true,
      });
      this.el.dispatchEvent(customEvent);
    }
    this.visible = false;
  }

  render() {
    return (
      <div>
        <ix-style-loader></ix-style-loader>
        {this.visible && (
          <ul class="menu" style={{ top: `${this.pos.y}px`, left: `${this.pos.x}px` }}>
            {this.items.map((item) => (
              <li
                class={{ disabled: !item.enabled }}
                onClick={() => this.onItemClick(item)}
              >
                <span class="icon">{item.icon}</span>
                <span>{item.name}</span>
              </li>
            ))}
          </ul>
        )}
        <slot></slot>
      </div>
    );
  }
}
